import React, { Component } from 'react';

import icon from './tire_preasure.svg';

import classes from './status_light.css';

class StatusLight extends Component {
  render() {
    return (
      <div className={classes.statusLight}>
        <div className={classes.iconContainer}>
          <img src={this.props.src || icon} />
        </div>
      </div>
    );
  }
}

export default StatusLight;



