import React, { Component } from 'react';

import classes from './navigation_map.css';

class NavigationMap extends Component {
  render() {
    return (
      <div className={classes.navigationMap}>
        <div className={classes.map}>
        </div>
      </div>
    );
  }
}

export default NavigationMap;



