import React, { Component } from 'react';
import Sound from 'react-sound';
import { CSSTransitionGroup } from 'react-transition-group';

import NotificationToast, {
  colors,
} from '../notification_toast/notification_toast';

import soundSrc from './alert.oggvorbis.ogg';
import tempHighSrc from './tempature.svg';
import tirePreasureSrc from './tire_preasure.svg';
import petrolSrc from './petrol.svg';
import fullBeamSrc from './full_beam.svg';
import classes from './notifications.css';

const ENGLISH_MESSAGES = {
  'LOW_PETROL': 'Petrol is running low',
  'LOW_TYRE_PRESSURE': 'Tyre air preasure is low in the front left wheel',
  'HIGH_TEMP': 'Engine tempature is too high',
  'FULL_BEAM': 'Full beam lights turned on',
};

const CHINESE_MESSAGES = {
  'LOW_PETROL': '汽油量不足',
  'LOW_TYRE_PRESSURE': '左前轮胎内气压不足',
  'HIGH_TEMP': '发动机温度过高',
};

class Notifications extends Component {
  constructor(props) {
    super(props);

    this.state = {
      show: false,
      closed: true,
    };
  }

  componentDidMount() {
    setTimeout(this.showNotification.bind(this), 5000);
  }

  showNotification() {
    this.timeout = setTimeout(() => {
      this.setState({ closed: false });

      this.timeout = setTimeout(() => {
        this.hideNotification();
      }, 5000);
    }, 1000);

    this.setState({ show: true, showing: true });
  }

  hideNotification() {
    this.timeout = setTimeout(() => {
      this.setState({ show: false });
      this.props.onNotificationFinish();
    }, 300);

    this.setState({ closed: true, showing: false });
  }

  toggleNotification() {
    clearTimeout(this.timeout);

    if (this.state.show) {
      this.hideNotification();
    } else {
      this.showNotification();
    }
  }

  render() {
    let notification;
    let sound;

    if (this.state.show) {
      notification = (
        <div className={classes.notificationContainer}>
          <NotificationToast
            color={colors.WARNING}
            icon={tirePreasureSrc}
            closed={this.state.closed}
          >
            {CHINESE_MESSAGES['LOW_TYRE_PRESSURE']}
          </NotificationToast>
        </div>
      );
    }

    if (this.state.show && this.state.closed && this.state.showing) {
      sound = <Sound url={soundSrc} playStatus={Sound.status.PLAYING}/>;
    }

    return (
      <div
        className={classes.notifications}
        onClick={this.toggleNotification.bind(this)}
      >
        {sound}
        <CSSTransitionGroup
          className={classes.transitionGroup}
          transitionName={{
            enter: classes.transitionEnter,
            enterActive: classes.transitionEnterActive,
            leave: classes.transitionLeave,
            leaveActive: classes.transitionLeaveActive,
            appear: classes.transitionAppear,
            appearActive: classes.transitionAppearActive,
          }}
          transitionEnterTimeout={500}
          transitionLeaveTimeout={300}
        >
          {notification}
        </CSSTransitionGroup>
      </div>
    );
  }
}

Notifications.propTypes = {
  closed: React.PropTypes.bool,
};

export default Notifications;



