import React, { Component } from 'react';

import classes from './notification_toast.css';

export const colors = {
  WARNING: '#FF8484',
  LIGHTS: '#84D2FF',
  DEFAULT: '#D8E4EA',
};

class NotificationToast extends Component {
  constructor(props) {
    super(props);

    this.state = {
      closed: props.closed,
    };
  }

  componentWillReceiveProps(nextProps) {
    this.setState(nextProps);
  }

  componentDidMount() {
    setTimeout(() => {
      this.setState({
        width: this.getContentsWidth(),
      });
    }, 0);
  }

  getContentsWidth() {
    return this.refs.contents.clientWidth;
  }

  render() {
    const notificationClass = this.state.closed ?
      classes.notificationToastClosed :
      classes.notificationToast;

    const textClass = this.state.closed ?
      classes.textClosed :
      classes.text;

    const style = {
      color: this.props.color,
      width: this.state.closed ? 62 : this.state.width,
    };

    return (
      <div className={notificationClass} style={style}>
        <div className={classes.ripple}></div>
        <div className={classes.contents} ref="contents">
          <div className={classes.iconContainer}>
            <div className={classes.icon} ref="icon">
              <img className={classes.img} src={this.props.icon}/>
            </div>
          </div>
          <div className={textClass}>
            {this.props.children}
          </div>
        </div>
      </div>
    );
  }
}

NotificationToast.propTypes = {
  closed: React.PropTypes.bool,
};

export default NotificationToast;



