import React, { Component } from 'react';

// import Dial from './dial/dial';
import StatusBar from './status_bar/status_bar';
import Notifications from './notifications/notifications';
import NavigationMap from './navigation_map/navigation_map';

import classes from './App.css';


class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      statusCount: 0,
    };
  }

  onNotificationFinish() {
    this.setState({
      statusCount: 1,
    });
  }

  render() {
    return (
      <div className={classes.app}>
        <div className={classes.container}>
          <div className={classes.statusBarContainer}>
            <StatusBar count={this.state.statusCount}/>
          </div>

          <div className={classes.notificationContainer}>
            <Notifications
              onNotificationFinish={this.onNotificationFinish.bind(this)}
            />
          </div>
        </div>
      </div>
    );
  }
}

export default App;



