import React, { Component } from 'react';
import { CSSTransitionGroup } from 'react-transition-group';
import { times } from 'lodash';

import StatusLight from '../status_light/status_light';
import fullBeamSrc from '../notifications/full_beam.svg';

import classes from './status_bar.css';

class StatusBar extends Component {
  constructor() {
    super();

    this.state = {
      count: 0,
    };
  }

  componentWillReceiveProps(nextProps) {
    this.setState(nextProps);
  }

  add() {
    this.setState({
      count: this.state.count + 1,
    })
  }

  renderChildren() {
    const items = times(this.state.count, (i) => {
      return (
        <div key={i} className={classes.segment}>
          <div className={classes.item}>
            <StatusLight />
          </div>
        </div>
      );
    });

    return items;
  }

  render() {
    return (
      <div className={classes.statusBar} onClick={this.add.bind(this)}>
        <CSSTransitionGroup
          className={classes.transitionGroup}
          transitionName={{
            enter: classes.transitionEnter,
            enterActive: classes.transitionEnterActive,
            leave: classes.transitionLeave,
            leaveActive: classes.transitionLeaveActive,
            appear: classes.transitionAppear,
            appearActive: classes.transitionAppearActive,
          }}
          transitionEnterTimeout={500}
          transitionLeaveTimeout={300}>
          {this.renderChildren()}
          <StatusLight src={fullBeamSrc}/>
        </CSSTransitionGroup>
      </div>
    );
  }
}

export default StatusBar;



